import axios from "axios";
import { authHeader } from "./auth-header";

const API_URL = "http://localhost:5000";

const getUserData = () => {
  return axios.get(`${API_URL}/results/`, { headers: authHeader() });
};

const getUserExperiments = () => {
  return axios.get(`${API_URL}/experiments/`, { headers: authHeader() });
};

const getUserExperimentResults = (experimentId) => {
  return axios.get(`${API_URL}/experiments/${experimentId}`, {
    headers: authHeader(),
  });
};

const addBruteforceTask = (list, r, repetitions) => {
  return axios.post(
    `${API_URL}/bruteforce`,
    { list, r, repetitions },
    { headers: authHeader() }
  );
};

const addKKTask = (list, r, repetitions) => {
  return axios.post(
    `${API_URL}/kk`,
    { list, r, repetitions },
    { headers: authHeader() }
  );
};

export const userService = {
  getUserData,
  getUserExperiments,
  getUserExperimentResults,
  addBruteforceTask,
  addKKTask,
};

export default userService;
