import React, { useState, useEffect, useRef } from "react";
import { Button, Form, Container, Row, Col, Spinner } from "react-bootstrap";
import * as d3 from "d3";
import "d3-random";
import { nest } from "d3-collection";

import Experiments from "./Experiments";
import RandomNumberGenerator from "./RandomNumberGenerator";
import UserService from "../services/user.service";

const BoardUser = () => {
  const [loading, setLoading] = useState(false);
  const [experiments, setExperiments] = useState([]);
  const [checkedExperimentsDict, setCheckedExperimentsDict] = useState({});
  const [selectedResult, setSelectedResult] = useState(null);

  const valuesRef = useRef();
  const rRef = useRef();
  const repetitionsRef = useRef();
  const algoRef = useRef();
  const chartContainerRef = useRef();

  const refreshExperiments = () => {
    setLoading(true);
    UserService.getUserExperiments()
      .then((response) => {
        setExperiments(response.data.experiments);
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  const handleSelectAllExperiments = (algo) => {
    const newCheckedDict = experiments
      .filter((experiment) => experiment.algo === algo)
      .reduce((acc, current) => {
        return { ...acc, [current.id]: true };
      }, {});
    setCheckedExperimentsDict({ ...checkedExperimentsDict, ...newCheckedDict });
  };

  const handleDeselectAllExperiments = (algo) => {
    const newCheckedDict = experiments
      .filter((experiment) => experiment.algo === algo)
      .reduce((acc, current) => {
        return { ...acc, [current.id]: false };
      }, {});
    setCheckedExperimentsDict({ ...checkedExperimentsDict, ...newCheckedDict });
  };

  useEffect(() => {
    refreshExperiments();
  }, []);

  useEffect(() => {
    const newExperimentsCheckedDict = experiments.reduce((acc, current) => {
      return { ...acc, [current.id]: false };
    }, {});
    Object.entries(checkedExperimentsDict).forEach(([key, value]) => {
      if (newExperimentsCheckedDict.hasOwnProperty(key)) {
        newExperimentsCheckedDict[key] = value;
      }
    });
    setCheckedExperimentsDict(newExperimentsCheckedDict);
  }, [experiments]);

  useEffect(() => {
    console.log("length");
    d3.select(chartContainerRef.current).selectAll("svg.length").remove();
    const data = Object.entries(checkedExperimentsDict).reduce(
      (acc, [key, value]) => {
        const findResult = experiments.find((result) => {
          return result.id === Number(key);
        });

        if (value) {
          const algo = findResult.algo;
          const r = findResult.r;
          const computationTime = findResult.avgComputationTime;
          const setValues = findResult.setValues;
          return [
            ...acc,
            { ...findResult, algo, r, computationTime, setValues },
          ];
        }
        return acc;
      },
      []
    );

    console.log(data);

    if (data.length === 0) {
      return;
    }

    const margin = { top: 10, right: 100, bottom: 30, left: 100 },
      width = 1000 - margin.left - margin.right,
      height = 600 - margin.top - margin.bottom;

    const svg = d3
      .select(chartContainerRef.current)
      .append("svg")
      .attr("class", "length")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    const sumstat = nest()
      .key((d) => d.algo)
      .sortValues((a, b) => (a.computationTime > b.computationTime ? 1 : -1))
      .entries(data);

    const x = d3
      .scaleLinear()
      .domain([0, d3.max(data, (d) => Math.max(...d.computationTimes))])
      .range([0, width]);
    svg
      .append("g")
      .attr("transform", "translate(0," + height + ")")
      .call(d3.axisBottom(x).ticks(5));

    const y = d3
      .scaleLinear()
      .domain([0, d3.max(data, (d) => d.setValues.length)])
      .range([height, 0]);
    svg.append("g").call(d3.axisLeft(y));

    const res = sumstat.map(function (d) {
      return d.key;
    }); // list of group names
    const color = d3
      .scaleOrdinal()
      .domain(res)
      .range([
        "#e41a1c",
        "#377eb8",
        "#4daf4a",
        "#984ea3",
        "#ff7f00",
        "#ffff33",
        "#a65628",
        "#f781bf",
        "#999999",
      ]);

    svg
      .selectAll(".line")
      .data(sumstat)
      .enter()
      .append("path")
      .attr("fill", "none")
      .attr("stroke", function (d) {
        return color(d.key);
      })
      .attr("stroke-width", 1.5)
      .attr("d", function (d) {
        return d3
          .line()
          .curve(d3.curveCatmullRom)
          .x(function (d) {
            return x(d.computationTime);
          })
          .y(function (d) {
            return y(d.setValues.length);
          })(d.values);
      });

    svg
      .selectAll("dot")
      .data(data)
      .enter()
      .append("circle")
      .attr("r", 4)
      .attr("cx", function (d) {
        return x(d.computationTime);
      })
      .attr("cy", function (d) {
        return y(d.setValues.length);
      })
      .on("click", (e, d) => {
        setSelectedResult(d);
      });

    res.forEach((res, i) => {
      svg
        .append("text")
        .attr("x", width + 20) // space legend
        .attr("y", margin.top + 20 + i * 20)
        .attr("class", "legend") // style the legend
        .style("fill", function () {
          // Add the colours dynamically
          return color(res);
        })

        .text(res);
    });

    //Draw boxplot
    const boxplotGroups = svg
      .selectAll("g.boxplot")
      .data(data)
      .enter()
      .append("g")
      .attr("class", "boxplot");

    boxplotGroups.each((d, i, g) => {
      const boxplotGroup = d3.select(g[i]);
      const sortedComputationTimes = d.computationTimes.sort(d3.ascending);
      const q1 = d3.quantile(sortedComputationTimes, 0.25);
      const median = d3.quantile(sortedComputationTimes, 0.5);
      const q3 = d3.quantile(sortedComputationTimes, 0.75);
      const IQR = q3 - q1;
      const min = q1 - 1.5 * IQR;
      const max = q3 + 1.5 * IQR;

      //Main line
      boxplotGroup
        .append("line")
        .attr("stroke", "black")
        .attr("x1", x(min))
        .attr("x2", x(max))
        .attr("y1", y(d.setValues.length))
        .attr("y2", y(d.setValues.length));

      //Box
      const boxplotHeight = 20;
      boxplotGroup
        .append("rect")
        .attr("x", x(q1))
        .attr("y", y(d.setValues.length) - boxplotHeight / 2)
        .attr("height", boxplotHeight)
        .attr("width", x(q3) - x(q1))
        .attr("stroke", "black")
        .attr("fill", "#69b3a2");

      //Lines
      boxplotGroup
        .selectAll("line.indicator")
        .data([min, median, max])
        .enter()
        .append("line")
        .attr("class", "indicator")
        .attr("x1", (dd) => x(dd))
        .attr("x2", (dd) => x(dd))
        .attr("y1", y(d.setValues.length) - boxplotHeight / 2)
        .attr("y2", y(d.setValues.length) + boxplotHeight / 2)
        .attr("stroke", "black");

      //Individual points
      boxplotGroup
        .selectAll("dot")
        .data(sortedComputationTimes)
        .enter()
        .append("circle")
        .attr("r", 3)
        .attr("cx", (dd) => x(dd))
        .attr("cy", y(d.setValues.length))
        .attr("stroke", "black")
        .attr("fill", "white");
    });
  }, [checkedExperimentsDict]);

  useEffect(() => {
    d3.select(chartContainerRef.current).selectAll("svg.r").remove();
    const data = Object.entries(checkedExperimentsDict).reduce(
      (acc, [key, value]) => {
        const findResult = experiments.find((result) => {
          return result.id === Number(key);
        });

        if (value) {
          const algo = findResult.algo;
          const r = findResult.r;
          const computationTime = findResult.avgComputationTime;
          const setValues = findResult.setValues;
          return [
            ...acc,
            { ...findResult, algo, r, computationTime, setValues },
          ];
        }
        return acc;
      },
      []
    );

    console.log(data);

    if (data.length === 0) {
      return;
    }

    const margin = { top: 10, right: 100, bottom: 30, left: 100 },
      width = 1000 - margin.left - margin.right,
      height = 600 - margin.top - margin.bottom;

    const svg = d3
      .select(chartContainerRef.current)
      .append("svg")
      .attr("class", "r")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    const sumstat = nest()
      .key((d) => d.algo)
      .sortValues((a, b) => (a.computationTime > b.computationTime ? 1 : -1))
      .entries(data);

    const x = d3
      .scaleLinear()
      .domain([0, d3.max(data, (d) => Math.max(...d.computationTimes))])
      .range([0, width]);
    svg
      .append("g")
      .attr("transform", "translate(0," + height + ")")
      .call(d3.axisBottom(x).ticks(5));

    const y = d3
      .scaleLinear()
      .domain([0, d3.max(data, (d) => d.r)])
      .range([height, 0]);
    svg.append("g").call(d3.axisLeft(y));

    const res = sumstat.map(function (d) {
      return d.key;
    }); // list of group names
    const color = d3
      .scaleOrdinal()
      .domain(res)
      .range([
        "#e41a1c",
        "#377eb8",
        "#4daf4a",
        "#984ea3",
        "#ff7f00",
        "#ffff33",
        "#a65628",
        "#f781bf",
        "#999999",
      ]);

    svg
      .selectAll(".line")
      .data(sumstat)
      .enter()
      .append("path")
      .attr("fill", "none")
      .attr("stroke", function (d) {
        return color(d.key);
      })
      .attr("stroke-width", 1.5)
      .attr("d", function (d) {
        return d3
          .line()
          .curve(d3.curveCatmullRom)
          .x(function (d) {
            return x(d.computationTime);
          })
          .y(function (d) {
            return y(d.r);
          })(d.values);
      });

    svg
      .selectAll("dot")
      .data(data)
      .enter()
      .append("circle")
      .attr("r", 4)
      .attr("cx", function (d) {
        return x(d.computationTime);
      })
      .attr("cy", function (d) {
        return y(d.r);
      })
      .on("click", (e, d) => {
        setSelectedResult(d);
      });

    res.forEach((res, i) => {
      svg
        .append("text")
        .attr("x", width + 20) // space legend
        .attr("y", margin.top + 20 + i * 20)
        .attr("class", "legend") // style the legend
        .style("fill", function () {
          // Add the colours dynamically
          return color(res);
        })

        .text(res);
    });

    //Draw boxplot
    const boxplotGroups = svg
      .selectAll("g.boxplot")
      .data(data)
      .enter()
      .append("g")
      .attr("class", "boxplot");

    boxplotGroups.each((d, i, g) => {
      const boxplotGroup = d3.select(g[i]);
      const sortedComputationTimes = d.computationTimes.sort(d3.ascending);
      const q1 = d3.quantile(sortedComputationTimes, 0.25);
      const median = d3.quantile(sortedComputationTimes, 0.5);
      const q3 = d3.quantile(sortedComputationTimes, 0.75);
      const IQR = q3 - q1;
      const min = q1 - 1.5 * IQR;
      const max = q3 + 1.5 * IQR;

      //Main line
      boxplotGroup
        .append("line")
        .attr("stroke", "black")
        .attr("x1", x(min))
        .attr("x2", x(max))
        .attr("y1", y(d.r))
        .attr("y2", y(d.r));

      //Box
      const boxplotHeight = 20;
      boxplotGroup
        .append("rect")
        .attr("x", x(q1))
        .attr("y", y(d.r) - boxplotHeight / 2)
        .attr("height", boxplotHeight)
        .attr("width", x(q3) - x(q1))
        .attr("stroke", "black")
        .attr("fill", "#69b3a2");

      //Lines
      boxplotGroup
        .selectAll("line.indicator")
        .data([min, median, max])
        .enter()
        .append("line")
        .attr("class", "indicator")
        .attr("x1", (dd) => x(dd))
        .attr("x2", (dd) => x(dd))
        .attr("y1", y(d.r) - boxplotHeight / 2)
        .attr("y2", y(d.r) + boxplotHeight / 2)
        .attr("stroke", "black");

      //Individual points
      boxplotGroup
        .selectAll("dot")
        .data(sortedComputationTimes)
        .enter()
        .append("circle")
        .attr("r", 3)
        .attr("cx", (dd) => x(dd))
        .attr("cy", y(d.r))
        .attr("stroke", "black")
        .attr("fill", "white");
    });
  }, [checkedExperimentsDict]);

  return (
    <div className="container">
      <header className="jumbotron">
        <h3>Experiments</h3>
        <Form
          onSubmit={(event) => {
            event.preventDefault();
            event.stopPropagation();

            const list = valuesRef.current.value
              .split(",")
              .map((value) => Number(value));
            const r = Number(rRef.current.value);
            const repetitions = repetitionsRef.current.value;

            if (algoRef.current.value === "Bruteforce") {
              UserService.addBruteforceTask(list, r, repetitions);
            } else if (algoRef.current.value === "Karmarkar-Karp") {
              UserService.addKKTask(list, r, repetitions);
            }
          }}
        >
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Values</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter values"
              ref={valuesRef}
            />
            <Form.Text className="text-muted">
              Each value should be separated by a coma.
            </Form.Text>
          </Form.Group>
          <RandomNumberGenerator valuesRef={valuesRef} />

          <Form.Group controlId="formBasicPassword">
            <Form.Label>Number of partitions (R)</Form.Label>
            <Form.Control
              type="text"
              placeholder="Number of partitions"
              ref={rRef}
            />
          </Form.Group>
          <Form.Group controlId="formBasicSelect">
            <Form.Label>Algorithm</Form.Label>
            <Form.Control as="select" custom ref={algoRef}>
              <option>Bruteforce</option>
              <option>Karmarkar-Karp</option>
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formBasicRepetitions">
            <Form.Label>Number of repetitions</Form.Label>
            <Form.Control
              type="text"
              placeholder="Number of repetitons"
              ref={repetitionsRef}
            />
          </Form.Group>
          <Button variant="success" type="submit">
            Add task
          </Button>
        </Form>
      </header>
      {selectedResult && (
        <div>
          Algorithm: {selectedResult.algo}
          <br />
          Values: {JSON.stringify(selectedResult.setValues)}
          <br />
          R: {JSON.stringify(selectedResult.r)}
          <br />
          Computation time: {selectedResult.computationTime}s
        </div>
      )}
      <Container fluid>
        <div ref={chartContainerRef}></div>
      </Container>
      <Container className="my-2">
        <Button
          variant="primary"
          disabled={loading}
          onClick={refreshExperiments}
        >
          {loading && (
            <Spinner
              as="span"
              animation="border"
              size="sm"
              role="status"
              aria-hidden="true"
            />
          )}
          {loading ? "" : "Refresh experiments"}
        </Button>
        <Row>
          <Col>
            <p>Bruteforce</p>
            <Experiments
              experiments={experiments.filter(
                (experiment) => experiment.algo === "bruteforce"
              )}
              checkedDict={checkedExperimentsDict}
              setCheckedDict={setCheckedExperimentsDict}
              handleSelectAll={() => handleSelectAllExperiments("bruteforce")}
              handleDeselectAll={() =>
                handleDeselectAllExperiments("bruteforce")
              }
            />
          </Col>
          <Col>
            <p>KK</p>
            <Experiments
              experiments={experiments.filter(
                (experiment) => experiment.algo === "kk"
              )}
              checkedDict={checkedExperimentsDict}
              setCheckedDict={setCheckedExperimentsDict}
              handleSelectAll={() => handleSelectAllExperiments("kk")}
              handleDeselectAll={() => handleDeselectAllExperiments("kk")}
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default BoardUser;
