import React, { useState, useRef, useEffect } from "react";
import { Form, Button } from "react-bootstrap";

import { DISTRIBUTIONS, PARAMETERS_CONFIG } from "./config";

export const RandomNumberGenerator = (props) => {
  const { valuesRef } = props;
  const [roundValues, setRoundValues] = useState(false);
  const [distribution, setDistribution] = useState(DISTRIBUTIONS.UNIFORM);
  const lenghtRef = useRef();
  const parameterRef = useRef([]);

  const generateInput = () => {
    const length = Number(lenghtRef.current.value);
    const parameters = parameterRef.current.map((el) =>
      el ? Number(el.value) : undefined
    );
    console.log(parameters);
    const randomGenerator = PARAMETERS_CONFIG[distribution].GENERATOR(
      ...parameters
    );
    const array = [];
    for (let i = 0; i < length; i++) {
      const random = randomGenerator();
      const randomRounded = Math.round(random);
      array.push(roundValues ? randomRounded : random);
    }
    const input = array.reduce(
      (curr, next, i) => curr + (i === 0 ? "" : ",") + next,
      ""
    );
    valuesRef.current.value = input;
  };

  return (
    <>
      <Form.Group>
        <Form.Label>Length</Form.Label>
        <Form.Control type="text" placeholder="Enter value" ref={lenghtRef} />
        <Form.Label>Distribution</Form.Label>
        <Form.Control
          as="select"
          onChange={(event) => setDistribution(event.target.value)}
        >
          {Object.entries(PARAMETERS_CONFIG).map(([key, value]) => (
            <option>{key}</option>
          ))}
        </Form.Control>
        <Form.Text className="text-muted">
          {PARAMETERS_CONFIG[distribution].DESCRIPTION}
        </Form.Text>
        {PARAMETERS_CONFIG[distribution].PARAMETERS.map((parameter, i) => (
          <>
            <Form.Label>{parameter}</Form.Label>
            <Form.Control
              type="text"
              placeholder={parameter}
              ref={(el) => (parameterRef.current[i] = el)}
            />
          </>
        ))}
        <Form.Check
          type="checkbox"
          label="Round values?"
          value={roundValues}
          onChange={() => setRoundValues(!roundValues)}
        />
        <Button onClick={generateInput} className="mt-3">
          Generate
        </Button>
      </Form.Group>
    </>
  );
};

export default RandomNumberGenerator;
