import * as d3 from "d3";
import "d3-random";

export const DISTRIBUTIONS = {
  UNIFORM: "Uniform",
  GAUSSIAN: "Gaussian",
  LOG_NORMAL: "Log-normal",
  BATES: "Bates",
  IRWIN_HALL: "Irwin-Hall",
  EXPONENTIAL: "Exponential",
  PARETO: "Pareto",
  BERNOULLI: "Bernoulli",
  GEOMETRIC: "Geometric",
  BINOMIAL: "Binomial",
  GAMMA: "Gamma",
  BETA: "Beta",
  WEIBULL: "Weibull",
  CAUCHY: "Cauchy",
  LOGISTIC: "Logistic",
  POISSON: "Poisson",
};

export const PARAMETERS_CONFIG = {
  [DISTRIBUTIONS.UNIFORM]: {
    PARAMETERS: ["min", "max"],
    DESCRIPTION:
      "The minimum allowed value of a returned number is min (inclusive), and the maximum is max (exclusive).",
    GENERATOR: d3.randomUniform,
  },
  [DISTRIBUTIONS.GAUSSIAN]: {
    PARAMETERS: ["mu", "sigma"],
    DESCRIPTION:
      "The expected value of the generated numbers is mu, with the given standard deviation sigma.",
    GENERATOR: d3.randomNormal,
  },
  [DISTRIBUTIONS.LOG_NORMAL]: {
    PARAMETERS: ["mu", "sigma"],
    DESCRIPTION:
      "The expected value of the random variable’s natural logarithm is mu, with the given standard deviation sigma.",
    GENERATOR: d3.randomLogNormal,
  },
  [DISTRIBUTIONS.BATES]: {
    PARAMETERS: ["n"],
    DESCRIPTION: "Bates distribution with n independent variables.",
    GENERATOR: d3.randomBates,
  },
  [DISTRIBUTIONS.IRWIN_HALL]: {
    PARAMETERS: ["n"],
    DESCRIPTION: "Irwin–Hall distribution with n independent variables.",
    GENERATOR: d3.randomIrwinHall,
  },
  [DISTRIBUTIONS.EXPONENTIAL]: {
    PARAMETERS: ["lambda"],
    DESCRIPTION:
      "Exponential distribution with the rate lambda; equivalent to time between events in a Poisson process with a mean of 1 / lambda. For example, exponential(1/40) generates random times between events where, on average, one event occurs every 40 units of time.",
    GENERATOR: d3.randomExponential,
  },
  [DISTRIBUTIONS.PARETO]: {
    PARAMETERS: ["alpha"],
    DESCRIPTION:
      "Pareto distribution with the shape alpha. The value alpha must be a positive value.",
    GENERATOR: d3.randomPareto,
  },
  [DISTRIBUTIONS.BERNOULLI]: {
    PARAMETERS: ["p"],
    DESCRIPTION:
      "Bernoulli distribution with 1 being returned with success probability p and 0 with failure probability q = 1 - p. The value p is in the range [0, 1].",
    GENERATOR: d3.randomBernoulli,
  },
  [DISTRIBUTIONS.GEOMETRIC]: {
    PARAMETERS: ["p"],
    DESCRIPTION:
      "Geometric distribution with success probability p. The value p is in the range [0, 1].",
    GENERATOR: d3.randomGeometric,
  },
  [DISTRIBUTIONS.BINOMIAL]: {
    PARAMETERS: ["n", "p"],
    DESCRIPTION:
      "Binomial distribution with n the number of trials and p the probability of success in each trial. The value n is greater or equal to 0, and the value p is in the range [0, 1].",
    GENERATOR: d3.randomBinomial,
  },
  [DISTRIBUTIONS.GAMMA]: {
    PARAMETERS: ["k", "theta"],
    DESCRIPTION:
      "Gamma distribution with k the shape parameter and theta the scale parameter. The value k must be a positive value.",
    GENERATOR: d3.randomGamma,
  },
  [DISTRIBUTIONS.BETA]: {
    PARAMETERS: ["alpha", "beta"],
    DESCRIPTION:
      "Beta distribution with alpha and beta shape parameters, which must both be positive.",
    GENERATOR: d3.randomBeta,
  },
  [DISTRIBUTIONS.WEIBULL]: {
    PARAMETERS: ["k", "a", "b"],
    DESCRIPTION: ` generalized extreme value distributions, depending on k:

      If k is positive, the Weibull distribution with shape parameter k
      If k is zero, the Gumbel distribution
      If k is negative, the Fréchet distribution with shape parameter −k
      
      In all three cases, a is the location parameter and b is the scale parameter.`,
    GENERATOR: d3.randomWeibull,
  },
  [DISTRIBUTIONS.CAUCHY]: {
    PARAMETERS: ["a", "b"],
    DESCRIPTION: `Cauchy distribution. a and b have the same meanings as in Weibull.`,
    GENERATOR: d3.randomCauchy,
  },
  [DISTRIBUTIONS.LOGISTIC]: {
    PARAMETERS: ["a", "b"],
    DESCRIPTION: `Logistic distribution. a and b have the same meanings and default values as in d3.randomWeibull.`,
    GENERATOR: d3.randomLogistic,
  },
  [DISTRIBUTIONS.POISSON]: {
    PARAMETERS: ["lambda"],
    DESCRIPTION: `Poisson distribution with mean lambda.`,
    GENERATOR: d3.randomPoisson,
  },
};
