import React, { useState } from "react";
import { Accordion, Card, Form, Button, ListGroup } from "react-bootstrap";
import InfiniteScroll from "react-infinite-scroll-component";

import UserService from "../services/user.service";

export const Experiments = (props) => {
  const {
    experiments,
    checkedDict,
    setCheckedDict,
    handleSelectAll,
    handleDeselectAll,
  } = props;

  const [loadedResults, setLoadedResults] = useState({});

  const loadResultsForExperiment = (experimentID) => {
    UserService.getUserExperimentResults(experimentID).then((response) => {
      setLoadedResults({
        ...loadedResults,
        [experimentID]: response.data.results,
      });
    });
  };

  return (
    <>
      <Button className="mr-2 mb-2" onClick={handleSelectAll}>
        Select All
      </Button>
      <Button className="mr-2 mb-2" onClick={handleDeselectAll}>
        Deselect All
      </Button>
      <Accordion>
        {experiments.map((experiment, i) => (
          <Card key={experiment.id}>
            <Card.Header className="d-flex align-items-center">
              <Form.Check
                type="checkbox"
                id={experiment.id}
                onChange={(e) => {
                  setCheckedDict({
                    ...checkedDict,
                    [experiment.id]: !checkedDict[experiment.id],
                  });
                }}
                checked={checkedDict[experiment.id]}
              />
              <Accordion.Toggle
                as={Button}
                variant="link"
                eventKey={experiment.id}
              >
                Values: {JSON.stringify(experiment.setValues)}, R:{" "}
                {experiment.r}
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey={experiment.id}>
              <InfiniteScroll height={300} dataLength={1}>
                <Card.Body>
                  ExperimentID: {experiment.id}
                  <br />
                  Number of trials: {experiment.repetitions}
                  <br />
                  Number of finished trials:{" "}
                  {experiment.computationTimes.length}
                  <br />
                  <Button
                    onClick={() => loadResultsForExperiment(experiment.id)}
                  >
                    Load results
                  </Button>
                  <br />
                  {loadedResults[experiment.id] && (
                    <>
                      <p>
                        Distance: {loadedResults[experiment.id].minDistance}
                      </p>
                      <ol>
                        {loadedResults[experiment.id].bestPartitions.map(
                          (partition, i) => (
                            <li key={i}>{JSON.stringify(partition)}</li>
                          )
                        )}
                      </ol>
                    </>
                  )}
                </Card.Body>
              </InfiniteScroll>
            </Accordion.Collapse>
          </Card>
        ))}
      </Accordion>
    </>
  );
};

export default Experiments;
